.navbar {
  height: 60px;
  display: flex;
  align-items: center;
  background-color: #fffaee;
  position: sticky;
  top: 0;
  z-index: 100;
  justify-content: space-between;
}

.navbar_logo {
  height: 5rem; 
  width: 5rem;
  object-fit: contain;
}

.nav {
  display: flex;
  flex: 1;
  align-items: center;
  margin-right: 2rem;
}

.nav a {
  text-decoration: none;
  color: black;
  margin-left: 5rem;
  font-weight: bold; font-size: 1.2rem;
}

.nav a:hover,
.nav a:active {
  color: grey;
}
